import React,{Component} from "react"
import render from "react-dom"
import {updateTop} from "../containers/PreviewBox.js"
export const LiCheckbox=({onclick})=>{
    return( 
        <li onClick={()=>{onclick("CHECKBOX")}}><i className="iconfont icon-checkbox"></i></li>
    )
}
const CheckboxPreviewLI=({data})=>{
    return <label className="radio"><input type="checkbox" disabled="disabled" checked={data.checked}/>{data.value?data.value:""}</label>
}
const CheckboxFieldAppendLI=({data,actions,index,id})=>{
    let radio,input
    return <li className="flexCenter">
                  <input type="checkbox" name="radio" checked={data.checked} ref={el=>{radio=el}} onChange={()=>{actions.changeValue("choicesChecked",radio.checked,id,index,"checkbox")}} />
                  <label className="labeldiv">
                    <input type="text"  placeholder="" value={data.value} ref={el=>{input=el}} onChange={()=>{actions.changeValue("choicesInput",input.value,id,index)}}/>
                  </label>
                  <span className="cz"> 
                    <a className="fa fa-plus-square-o addBtn" onClick={()=>{actions.changeValue("addItem","",id,index)}}></a>
                    <a className="fa fa-minus-square-o delBtn"  onClick={()=>{actions.changeValue("delItem","",id,index)}}></a>
                  </span>
            </li>

}
export const PreviewCheckbox=({data,actions})=>{
    var radioItem=[];
    for(var i=0,len=data.choices.length;i<len;i++){
        radioItem.push(<CheckboxPreviewLI key={i} data={data.choices[i]}/>);
    };
    return <div className={data.active?"previewItem active":"previewItem"} onClick={()=>{actions.clickPreviewLi(data.id)}}>
              <div className="title">{data.title}<span className="red ml5">{data.required?"*":""}</span></div>
              <div className="readOnly">
                      {radioItem}
              </div>
              <div className="sort-handlerBox"><i className="icon sort-handler"></i> </div> 
              <div className="cz">
                   <i className="fa fa-plus-circle addBtn" title="" onClick={(e)=>{e.stopPropagation();actions.oncopy(data.id)}}></i>
                   <i className="fa fa-minus-circle delBtn" title="" onClick={(e)=>{e.stopPropagation();actions.ondelete(data.id)}}></i>
              </div>
           </div>
}
export class EditCheckbox extends Component{
    constructor(props) {
        super(props)
    }
    componentDidMount(){
        updateTop()
    }
    componentDidUpdate(){
        updateTop()
    }
    batchEditing(data){
      var datas="";
      if(data.length){
          data.map(el=>{
              if(el.value){
                datas+=el.value+'\n'
              }
          }
          )
      }
      console.log(this.props)
      this.props.modalBoxIsNone()
      this.props.actions.batchEdit(datas)
    }
    render(){
    let data=this.props.data;
    let actions=this.props.actions;
    let choices=this.props.choices;
    let title,defaultinput,tis,minValue,maxValue,required,readonly
    var radioItem=[];
    for(var i=0,len=data.choices.length;i<len;i++){
        radioItem.push(<CheckboxFieldAppendLI key={i} data={data.choices[i]} id={data.id} actions={actions} index={i} />);
    }
    return <div className="fieldEdit radioFieldEdit"  onClick={()=>{actions.clickPreviewLi(data.id)}}>
             <div className="title"><i className="fa fa-edit"></i></div>
             <div className="edit_item">   
                 <div className="tit"></div>   
                 <div className="write"> 
                      <input type="text" className="sf-tit-value" ref={el=>{title=el}} name="tit_value" value={data.title} onChange={()=>{actions.changeValue("title",title.value,data.id)}} placeholder=""/>
                 </div>
             </div>
             <div className="edit_item">  
                 <div className="tit"></div> 
                 <ul className="write appendLi">
                        {radioItem}
                      <div className="tr tianjiaDiv mt5">
                       
                        <a className="add_multiple_choices" href="javascript:;" onClick={()=>{this.batchEditing(data.choices)}}> </a>
                      </div>
                  </ul>
             </div>
             <div className="edit_item">  
                  <div className="tit"></div>  
                  <div className="write mt5">  
                      <select className="input-small" value={data.line_row} onChange={()=>{}}>     
                          <option value="1" ></option>     
                          <option value="2"></option>     
                          <option value="3"></option>   
                      </select>
                  </div>
            </div>
             <div className="edit_item">   
               <div className="tit"></div>   
               <div className="write">     
                   <ul>     
                       <li className=""><label className="checkbox"><input type="checkbox" ref={el=>{required=el}} 
                       onChange={()=>{actions.changeValue("required",required.checked,data.id)}} checked={data.required}/></label></li>     
                   </ul>   
               </div>
            </div>
        </div>
    }
}


